import React, {useState} from 'react';
import './TabMenu.css';

const TabMenu = (props) => {
  const [menuNum, setMenuNum] = useState(1);
  const tabMenuToggle = (e) => { // 메뉴 클릭 시 호출되는 함수
    setMenuNum(e);  // 상태값을 변화시키기 위한 state함수 호출
  }

  return (
    <>
      <h3>탭 메뉴 구현하기</h3>
      <nav className='menu_list'>
        <ul>
          {/* 삼항조건연산자 조건식?참:거짓 */}
          <li onClick={()=>tabMenuToggle(1)} className={menuNum===1?'act':''}>Menu1</li>
          <li onClick={()=>tabMenuToggle(2)} className={menuNum===2?'act':''}>Menu2</li>
          <li onClick={()=>tabMenuToggle(3)} className={menuNum===3?'act':''}>Menu3</li>
          <li onClick={()=>tabMenuToggle(4)} className={menuNum===4?'act':''}>Menu4</li>
        </ul>
      </nav>
      <div className='tab_content'>
        {menuNum===1?<div className='content'>내용1</div>:""}
        {menuNum===2?<div className='content'>내용2</div>:""}
        {menuNum===3?<div className='content'>내용3</div>:""}
        {menuNum===4?<div className='content'>내용4</div>:""}
      </div>
    </>
  );
}

export default TabMenu;