import React, { useState } from 'react';
import './Counter.css';

// function Counter2(props) {
const Counter2 = (props) => {
  // 변수 초기값
  const [num, setNum] = useState(1);

  const onPlus=()=>{
    setNum(num+1);
    document.getElementById('m_btn2').disabled = false;
  }
  const onMinus=()=>{
    setNum(num-1);
    if(num<=1) setNum(1);
    if(num<=2) document.getElementById('m_btn2').disabled = true;
  }

  return (
    <>
      <p>+, - 버튼을 클릭하면 숫자 증가, 감소하는 state값 작성하기</p>

      <div>숫자 출력 : <span className="number">{num}</span></div>
      <input type="button" value="+" onClick={onPlus} />
      <input type="button" value="-" onClick={onMinus} id="m_btn2" />
    </>
  );
}

export default Counter2;