import React, { Component } from 'react';
import './Counter.css';

class Counter1 extends Component {
  // 초기 변수 값 지정
  state = {n:1}

  // 1씩 증가
  onPlus = () => {
    this.setState({n:this.state.n+1})
    document.getElementById('m_btn').disabled = false;
  }

  // 1씩 감소
  onMinus = () => {
    this.setState({n:this.state.n-1})

    if(this.state.n <= 1){
      this.setState({n:1});
    }
    if(this.state.n <= 2){
      document.getElementById('m_btn').disabled = true;
    }
  }

  render() {
    return (
      <>
        <p>+, - 버튼을 클릭하면 숫자 증가, 감소하는 state값 작성하기</p>

        <div>숫자 출력 : <span className="number">{this.state.n}</span></div>
        <input type="button" value="+" onClick={this.onPlus} />
        <input type="button" value="-" onClick={this.onMinus} id="m_btn" />


        <p>자신 컴포넌트에서 유동적으로 변할 값은 state로 선언</p>
        <p>setState()함수는 state의 값을 변경하고자 할 때 사용하는 함수</p>
        <p>jsx문법에서는 이벤트를 사용하고자 할 때 카멜표시법을 사용한다.</p>
        <p>이벤트 값에는 함수객체를 넣어서 호출</p>
      </>
    );
  }
}

export default Counter1;