import React from 'react';
import dummy from './data/customer.json';

function Customer(props) {
  return (
    <>
      <h3>Customer json데이터 불러오기</h3>

      <ul>
        {dummy.map(customer=>(
          <li key={customer.c_name}>{customer.c_name}, {customer.c_address}, {customer.c_tel}</li>
        ))}
      </ul>
    </>
  );
}

export default Customer;