import React from 'react';

function Student({student, onRemove}) {
  return (
    <>
      <p><b>{student.id} 이름 :</b> {student.name} <b>나이 :</b> {student.age} <b>이메일 :</b> {student.email} <button onClick={() => onRemove(student.id)}>삭제</button></p>
      <hr />
    </>
  );
}

export default Student;