import React, {useState} from 'react';

function Form4(props) {
  const [formData, setFormData] = useState({
    name:'', id:''
  });

  const changeData = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev, // ES6 스프레드 연산자
      [name]: value
    }));
  }

  const onDelete = (e) => {
    setFormData({name:'',id:''});
  }

  const printName = (e) => {
    e.preventDefault();
  }

  return (
    <>
      <h4>4. 여러개의 input태그의 데이터 관리하기</h4>
      <ul>
        <li>e.target.value : 값에 접근하기</li>
        <li>e.target.name : name 속성값에 접근하기</li>
        <li>this.setState({`[변수명]:값`}); : 속성명에 변수값을 사용하려면 대문자로 표시해야 함.</li>
        <li>nameChange : 값을 입력하였을 때 발생되는 이벤트 함수</li>
        <li>state를 사용하여 입력값 관리</li>
        <li>onChange 이벤트를 통해 상태 업데이트</li>
        <li>useState함수를 사용하여 상태 관리값을 한꺼번에 선언하고 관리 할 수 있다.</li>
      </ul>

      <form onSubmit={printName}>
        <p>출력내용(이름) : </p>
        <input type="text" id="name" name="name" value={formData.name} onChange={changeData} />

        <p>출력내용(아이디) : </p>
        <input type="text" id="id" name="id" value={formData.id} onChange={changeData} />

        <p>입력하신 이름은 '{formData.name}'이고, 아이디는 '{formData.id}'입니다.</p>

        <input type="button" value='다시쓰기' onClick={onDelete} />
        <input type="submit" />
      </form>
    </>
  );
}

export default Form4;