import React, {useState, useRef} from 'react';
import './ref.css';

function Useref(props) {
  // 1. state를 선언
  const [inputs, setInputs] = useState({
    password: '',  // 패스워드 초기값
    clicked:false,  // 클릭 검사
    validated:false  // 유효성 검사
  });

  // 2. 비구조화 할당방식
  const {password, clicked, validated} = inputs;

  // 3. useRef 선언
  const passwordInput = useRef();

  // 4. 입력박스에 사용자가 패스워드를 입력하는 경우 실행하기 위한 함수
  const onInputChange = (e) => {
    setInputs({
      password:e.target.value // 사용자가 입력한 값을 변수에 담는다
    })
  }

  const onInputCheck = (e) => {
    e.preventDefault();
    setInputs({
      clicked:true,  // 클릭 시 값 변경
      validated:password==='1234' // 입력된 패스워드 값 일치 여부 확인
    });
    if(password!=='1234') passwordInput.current.focus();
  }

  const onInputReset = (e) => {
    e.preventDefault();
    setInputs({
      password:'',
      clicked:false,
      validated:false
    })
  }

  return (
    <>
      <h3>Ref, useRef</h3>
      <p>
        ref는 '개발자가 특정 DOM을 선택해야 하는 상황이 발생될 때 사용하는 기능'으로 클래스 컴포넌트에서 사용하지만 hooks에서 useRef함수가 추가되면서 함수형 컴포넌트에서도 사용이 가능합니다.
      </p>
      <p>javascript : dom선택 시 getElementById(), querySelector()</p>
      <p>jquery : dom선택 시 $('선택자')</p>
      <p>리액트의 JSX안에서 DOM에 id를 달면 해당 DOM을 렌더링 할 때 그대로 전달됩니다.</p>
      <p>컴포넌트가 여러개 생성되는 경우 같은 id가 중복돼 에러가 발생할 수 있습니다. 하지만 ref는 전역적으로 작동하지 않고, 컴포넌트 내부에서만 작동하기 때문에 이런 문제가 발생하지 않습니다.</p>

      <dl>
        <dt>DOM을 꼭 사용해야 하는 상황</dt>
        <dd>특정 input태그에 포커스 주기</dd>
        <dd>스크롤 박스를 조작하기</dd>
        <dd>Canvas에 그림 그리기</dd>
        <dd>기타 ...</dd>
      </dl>

      <h3>실습. 입력양식을 만들고 버튼 클릭 시 초점 처리하기</h3>
      <p>입력양식에 사용자가 내용을 입력하여 내용이 일치하면 '초록색'배경, 그렇지 않으면 '주황색' 배경으로 변경</p>
      <dl>
        <dt>ref, useRef를 사용하지 않는 폼태그의 문제점</dt>
        <dd>사용자가 클릭하고 값을 입력한 경우 아이디나 패스워드가 일치하면 상관없지만 틀린 경우 다시 입력할 수 있도록 초점을 이동해야 한다.</dd>
        <dd>그래서 특정 DOM을 선택하기 위해 ref, useRef함수를 사용하여 초점이 정확하게 맞춰지도록 해야 함.</dd>
      </dl>

      <form name="" method="" action="" className='formStyle'>
        <fieldset>
          <legend>폼태그 제목</legend>
          <p><input type="password" value={password} onChange={onInputChange} ref={passwordInput} className={clicked&&(validated?'success':'failure')} /></p>
          <p><input type="button" value="검증하기" onClick={onInputCheck} />
          <input type="button" value="초기화" onClick={onInputReset} /></p>
        </fieldset>
      </form>

      <hr />
      <p>현재 입력 값 : {password}</p>
    </>
  );
}

export default Useref;