import React, { useState } from 'react';

function Form1(props) {
  const [name, setName] = useState('');

  const nameChange = (e) => {
    setName(e.target.value);
  }
  const nameReset = () => {
    setName('');
    // document.getElementById('name').value = '';
  }

  return (
    <>
      <h4>1. 기본 입력 폼 양식</h4>
      <form>
        <p>이름을 작성하세요 - {name}</p>
        <input type="text" name="name" value={name} onChange={nameChange} id="name" />
        {/* <input type="reset" onClick={nameChange} /> */}
        <input type="button" onClick={nameReset} value="초기화" />
      </form>
      <ul>
        <li>input태그의 값이 변경되면 'onChange'이벤트를 사용하여 h4에 텍스트 값이 변경되는 내용을 작성하기</li>
        <li>HTML폼태그는 일반적으로 DOM에 의해 처리됨</li>
        <li>React폼태그는 일반적으로 컴포넌트에 의해 처리됨</li>
        <li>컴포넌트가 데이터를 처리하면 모든 데이터가 state에 저장됨</li>
      </ul>
    </>
  );
}

export default Form1;