import React from 'react';

function K_Random(props) {
  let ran = 1 + Math.floor(Math.random()*2);
  const ran_src = `${process.env.PUBLIC_URL}/images/random${ran}.png`;

  return (
    <>
      <div className='rbanner_wrap'>
        <img src={ran_src} alt="이벤트 배너"/>
      </div>
    </>
  );
}

export default K_Random;