import React, {useState} from 'react';

function Form5(props) {
  const [formdata, setFormData] = useState({ name:'', age:null });
  const [result, setResult]=useState({ name:'', age:null });

  const changeData = (e) =>{
    const {name, value} = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  }

  const dataSubmit = (e) =>{
    e.preventDefault();
    const age = formdata.age;
    if(!Number(age)){
      alert('나이는 숫자로 입력해야 합니다.');
    } else {
      setResult({name : `${formdata.name}`, age : `${age}`});
    }
  }

  return (
    <>
      <h4>5. 데이터 전송 시 유효성 검사하기</h4>
      <p>이름, 나이 입력 시 이름은 문자, 나이는 숫자로 입력해야 해 해당 데이터가 아니면 오류메세지가 나오도록 함</p>
      <p>if문을 사용하여 state값 중 나이가 숫자가 아니면 'alert 메서드'로 경고창을 띄움</p>
      <p>나이는 숫자데이터이기 때문에 Number 형으로 변환해야 함</p>

      <form onSubmit={dataSubmit}>
        <p>이름 : <strong>{result.name}</strong></p>
        <p>나이 : <strong>{result.age}</strong></p>

        <p>이름 입력 : 
          <input type="text" name="name" onChange={changeData} />
        </p>

        <p>나이 입력 : 
          <input type="text" name="age" onChange={changeData} />
        </p>

        <p>
          <button type="submit">전송하기</button>
        </p>
      </form>
    </>
  );
}

export default Form5;