import React, {useState} from 'react';

function Form2(props) {
  let header, header2;
  const [id, setId] = useState('');
  const [pw, setPw] = useState('');
  const idChange = (e) => {
    setId(e.target.value);
  }
  const pwChange = (e) => {
    setPw(e.target.value);
  }
  const onDataReset = (e) => {
    setId('');
    setPw('');
  }

  // 조건식을 사용하여 값이 있으면 출력
  if(id){
    header = <p>사용자가 입력한 ID는 {id}입니다.</p>;
  } else {
    header = '';
  }

  if(pw){
    header2 = <p>사용자가 입력한 PW는 {pw}입니다.</p>;
  } else {
    header2 = '';
  }

  return (
    <>
      <h4>2. if문으로 조건식 작성하기</h4>
      {header}
      <form>
        <label htmlFor="id_name">ID : </label><input type="text" id="id_name" name="id_name" value={id} onChange={idChange} /><br />
        {header2}
        <label htmlFor="pw_name">PW : </label><input type="text" id="pw_name" name="pw_name" value={pw} onChange={pwChange} />
        <p>
          <button type="button" onClick={onDataReset}>초기화</button>
        </p>
      </form>
    </>
  );
}

export default Form2;