import React, {useState, useRef} from 'react';
import Student from './Student';
import StudentInput from './StudentInput';

function StudentList(props) {
  // 배열 데이터 목록 출력하기
  const [students, setStudents] = useState([{
      id:'st001',
      name:'김남준',
      age:28,
      email:'rm@gmail.com'
    },
    {
      id:'st002',
      name:'김석진',
      age:30,
      email:'kim@gmail.com'
    },
    {
      id:'st003',
      name:'최명길',
      age:33,
      email:'gil@gmail.com'
    },
    {
      id:'st004',
      name:'오수정',
      age:22,
      email:'crystal@gmail.com'
    },
    {
      id:'st005',
      name:'정상현',
      age:24,
      email:'hyun@gmail.com'
    }
  ]);

  const nextId = useRef(6);
  // 입력 상자에서 사용할 값을 state 관리
  const [inputs, setInputs] = useState({
    name:'', age:'', email:''
  })

  // 비구조화 할당 방식으로 state값 재선언
  const {name, age, email} = inputs;

  // 1. 데이터 변경 상태 확인
  const onDataChange=(e)=>{
    // name과 value는 값이 변경되는 input 태그의 속성을 비구조화 할당한다.
    const {name, value} = e.target;

    // state값 변경
    setInputs({
      ...inputs, // 기존 배열에 추가
      [name]:value
    });
  }

  // 2. 데이터 추가함수
  const onCreate=()=>{
    if(!name.trim() || !email.trim() || !age){
      alert('입력 정보를 확인해주세요.');
      return;
    }

    // 데이터 추가 구문
    const student = {
      id:'st'+String(nextId.current).padStart(3, '0'),
      name,
      age:parseInt(age, 10),
      email
    }

    setStudents([...students,student]);
    setInputs({
      name:'', age:'', email:''
    })

    nextId.current += 1;
  }

  // 3. 데이터 삭제함수
  // 사용자가 클릭한 자료의 id값을 찾아 데이터 삭제하고 다시 새로운 배열을 만들어야 함
  const onRemove=(id)=>{
    // student.id가 id와 같은 데이터를 삭제
      setStudents(students.filter((student)=>student.id!==id));
  }

  return (
    <>
      <StudentInput name={name} age={age} email={email} onCreate={onCreate} onDataChange={onDataChange} />
    {/* 자식 컴포넌트로 출력하기
      <ul>
        <li>
          <Student student={students[0]} />
        </li>
        <li>
          <Student student={students[1]} />
        </li>
        <li>
          <Student student={students[2]} />
        </li>
        <li>
          <Student student={students[3]} />
        </li>
        <li>
          <Student student={students[4]} />
        </li>
      </ul>
    */}

    {/* 방법 3. map함수로 데이터 반복 출력 */}

      <ul>
    {/* 배열 데이터 중 고유값이 존재 하는 경우 아래와 같이 작성
        {students.map(student => (
          <li key={student.id}><Student student={student} /></li>
        ))}
    */}
    {/* 배열 데이터 중 고유값이 존재하지 않는 경우 아래와 같이 작성 (index 추가) */}
        {students.map((student, index) => (
          <li key={index}><Student student={student} onRemove={onRemove} /></li>
        ))}
      </ul>
    </>
  );
}

export default StudentList;