import React from 'react';
import dummy from './data/goods.json';

function Goods(props) {
  return (
    <>
      <h3>Goods json데이터 출력하기</h3>
      <ul>
        {dummy.map(goods=>(
          <li key={goods.g_code}>{goods.g_code}, {goods.g_name}, {goods.g_cost}</li>
        ))}
      </ul>
    </>
  );
}

export default Goods;