import React, { useState } from 'react';

const TabMenu4 = (props) => {
  const [activeTab, setActiveTab] = useState('Home');
  const tabs=[
    {title:'Home', content:'메인'},
    {title:'About', content:'소개'},
    {title:'Contect', content:'문의하기'}
  ];
  
  const activeTabContent = tabs.find((tab) => tab.title === activeTab)?.content;

  return (
    <>
      <h3>Tabmnu4 - 배열객체를 생성하여 map함수로 콘텐츠 출력하기(최적화)</h3>
      <ul className='menu_list'>
        {tabs.map((tab)=>(
          <li key={tab.title}
          onClick={()=>setActiveTab(tab.title)}
          className={tab.title===activeTab?'act':''}>
          {tab.title}
          </li>
        ))}
      </ul>
      <div className='content'>
          {activeTabContent}
      </div>
    </>
  );
}

export default TabMenu4;