import React from 'react';

function Event(props) {
  const shoot = () => {
    alert('슛');
  }
  const shoot2 = (txt) => {
    alert(txt);
  }
  return (
    <>
      <h3 className="title3">이벤트(EVENT)</h3>
      <dl>
        <dt>이벤트란?</dt>
        <dd>- 사용자가 웹브라우저에서 DOM요소들과 상호작용하는 것</dd>
        <dd>- React에서 이벤트는 HTML과 동일한 이벤트를 갖는다.</dd>
        <dd>- 이벤트는 DOM요소에서만 줄 수 있고, 우리가 생성한 컴포넌트에는 줄 수 없다.</dd>
      </dl>

      <h4>1. HTML에서의 이벤트 - 이벤트명 작성 시 모두 소문자로 작성</h4>
      <pre>
        요소 onclick='함수명();'
      </pre>

      <h4>2. React에서의 이벤트</h4>
      <pre>
{`        요소 onClick={함수명}
        요소 onClick={this.함수명}`}
      </pre>
      <p><button onClick={shoot}>버튼</button></p>
      <p><button onClick={()=>shoot2('매개변수 전달 출력')}>버튼</button></p>

    </>
  );
}

export default Event;