import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { faXmark } from '@fortawesome/free-solid-svg-icons'
import './App.css';
import Class0 from './250428/Class0';
import Class1 from './250429/Class1';
import Class2 from './250430/Class2';
import Class3 from './250430/Class3';
import Kurly from './250430/Kurly';
import Class4 from './250501/Class4';
import Class5 from './250501/Class5';
import Event from './250501/Event';
import Form from './250501_form/Form';
import Array1 from './250507_array/Array1';
import Yellow from './250507_array/Yellow';
import Array2 from './250508_array/Array2';
import Usestate from './250508_array/Usestate';
import Loginform from './250508_array/Loginform';
import Useref from './250508_array/Useref';
import Array3 from './250509_array/Array3';
import Json from './250512_json_Hooks/Json';
import Lifecycle from './250512_json_Hooks/Lifecycle';
import UseEffect1 from './250512_json_Hooks/UseEffect1';
import UseEffect2 from './250512_json_Hooks/UseEffect2';
import UseEffect3 from './250512_json_Hooks/UseEffect3';
import TabMenu from './250513_tab/TabMenu';
import TabMenu2 from './250513_tab/TabMenu2';
import TabMenu3 from './250513_tab/TabMenu3';
import TabMenu4 from './250513_tab/TabMenu4';
import Music from './250514_movie/Music';
import Modal from './250516_modal/Modal';
import Topbtn from './250519_topBtn/Topbtn';
import Refresh from './250519_topBtn/Refresh';
import { useState, useEffect } from 'react';

function App() {
  const [menuOpen, setMenuOpen] = useState(false);

  const menuItems = [
    {id:'day1', label:'1일차 학습'},
    {id:'day2', label:'2일차 학습'},
    {id:'day3', label:'3일차 학습'},
    {id:'day4', label:'4일차 학습'},
    {id:'day5', label:'5일차 학습'},
    {id:'day6', label:'6일차 학습'},
    {id:'day7', label:'7일차 학습'},
    {id:'day8', label:'8일차 학습'},
    {id:'day9', label:'9일차 학습'},
    {id:'day10', label:'10일차 학습'},
    {id:'day11', label:'11일차 학습'},
    {id:'day13', label:'13일차 학습'},
    {id:'day14', label:'14일차 학습'},
    {id:'day15', label:'15일차 학습'},
    {id:'day18', label:'18일차 학습'},
  ];

  const wheelStop = (e) => {
    e.preventDefault();  // 기본 스크롤 동작을 차단
  };

  useEffect(() => {
    if (menuOpen) {
      // 메뉴가 열릴 때만 wheel 이벤트 리스너 추가
      document.addEventListener('wheel', wheelStop, { passive: false });
    } else {
      // 메뉴가 닫힐 때는 wheel 이벤트 리스너 제거
      document.removeEventListener('wheel', wheelStop);
    }

    // 클린업: 컴포넌트가 언마운트되거나 메뉴가 바뀔 때 이벤트 리스너 제거
    return () => {
      document.removeEventListener('wheel', wheelStop);
    };
  }, [menuOpen]);  // menuOpen 상태가 바뀔 때마다 실행
  
  const handleMenuClick=(id)=>{
    const el = document.getElementById(id);
    if(el){
      const y = el.getBoundingClientRect().top + window.pageYOffset - 56;
      window.scrollTo({top:y, behavior:'smooth'});
      setMenuOpen(false);
    }
  }

  return (
    <>
    <header className="App">
      <h1>리액트 학습하기</h1>
      <button onClick={()=>setMenuOpen(!menuOpen)} style={{
        position:'fixed',
        top:'10px', left:'20px',
        cursor:'pointer',
        border:'none',
        background:'none',
        fontSize:'24px'
      }}><FontAwesomeIcon icon={faBars} /></button>
      <div onWheel={(e)=>wheelStop(e)} style={{position:'fixed',
        top:'0px', left:'0px',
        width:'100%', height:'100vh',
        background:'rgba(0,0,0,.8)',
        transition:'0.3s',
        transform: menuOpen?'translateX(0%)':'translateX(-100%)',
        zIndex:1000,
      }}>
      <div
        style={{
          position:'fixed',
          top:'0px', left:'0px',
          height:'100vh',
          width:'240px',
          background:'#eee',
          transform: menuOpen?'translateX(0%)':'translateX(-150%)',
          transition:'0.3s',
          zIndex:1001,
          paddingTop:'60px',
          boxShadow:'-2px 0 12px rgba(0,0,0,.5)',
        }}>
          <button onClick={()=>setMenuOpen(false)} style={{
            position:'absolute',
            top:'20px', right:'20px',
            cursor:'pointer',
            border:'none',
            background:'none',
            fontSize:'24px'
          }}><FontAwesomeIcon icon={faXmark} /></button>
          <ul style={{listStyle:'none'}}>
            {menuItems.map(item=>(
              <li key={item.id} style={{marginTop:'10px'}}>
                <button onClick={()=>handleMenuClick(item.id)} style={{
                  cursor:'pointer',
                  border:'none',
                  background:'none',
                  fontSize:'16px',
          }}>{item.label}</button>
              </li>
            ))}
          </ul>
        </div>
        </div>
    </header>

    <main>
      <h2 className="title3" id='day1'>1일차 학습</h2>
      <Class0 />
      <hr />
      <h2 className="title3" id='day2'>2일차 학습</h2>
      <Class1 />
      <hr />
      <h2 className="title3" id='day3'>3일차 학습</h2>
      <Class2 />
      <hr />
      <Class3 />
      <hr />
      <Kurly />
      <hr />
      <h2 className="title3" id='day4'>4일차 학습</h2>
      <Class4 />
      <hr />
      <Class5 />
      <hr />
      <Event />
      <hr />
      <h2 className="title3" id='day5'>5일차 학습 - 폼태그와 값 출력, 유효성 검사, 실습예제</h2>
      <Form />
      <hr />
      <h2 className="title3" id='day6'>6일차 학습 - 배열데이터</h2>
      <Array1 />
      <hr />
      <Yellow />
      <hr />
      <h2 className='title3' id='day7'>7일차 학습 - 배열데이터 복습, useState, useRef</h2>
      <Array2 />
      <hr />
      <Usestate />
      <hr />
      <Loginform />
      <hr />
      <Useref />
      <hr />
      <h2 className='title3' id='day8'>8일차 학습 - 배열데이터 입력, 출력, 추가, 삭제, 실습예제</h2>
      <Array3 />
      <hr />
      <h2 className='title3' id='day9'>9일차 학습 - DB에서 저장한 JSON데이터 출력하기, Hooks(useEffect)</h2>
      <Json />
      <hr />
      <Lifecycle />
      <hr />
      <UseEffect1 />
      <hr />
      <UseEffect2 />
      <hr />
      <UseEffect3 />
      <hr />
      <h2 className='title3' id='day10'>10일차 학습 - 탭 메뉴 만들기</h2>
      <TabMenu />
      <hr />
      <TabMenu2 />
      <hr />
      <TabMenu3 />
      <hr />
      <TabMenu4 />
      <hr />
      <h2 className='title3' id='day11'>11일차 학습 - json데이터(music)출력하기, api를 활용한 영화소개 페이지 구현하기</h2>
      <Music />
      <hr />
      <h2 className='title3' id='day13'>13일차 - 사용자 UI - 모달 윈도(Modal Window)</h2>
      <Modal />
      <hr />
      <h2 className='title3' id='day14'>14일차 - 사용자 UX/UI-top버튼, 모바일 토글 메뉴, 새로고침 기능 추가</h2>
      <Topbtn />
      <Refresh />
      <hr />
      <h2 className='title3' id='day15'>15일차 - 모바일 토글 메뉴 배경색 추가, Frontend, Backend서버에서 글입력, 추가, 삭제 기능 구현하기</h2>
      <p>1. 학습목차 페이지 헤더영역 토글버튼 클릭 시 메뉴 배경색 어둡게 추가 수정</p>
      <p>2. Frontend, Backend http요청에 의해 데이터 입력/추가/삭제/수정 구현하기</p>
      <p>3. 실습응용 - Bookstore 테이블을 활용하여 리스트 출력, 입력/추가/삭제/수정 구현하기</p>
      <p>4. 실습응용 - Fruit 테이블을 활용하여 리스트 출력, 입력/추가/삭제/수정</p>
      <hr />
      <h2 className='title3' id='day18'>18일차 - 실습응용 - 관리자에게 문의하기, 로그인</h2>
      <p>1. 문의하기 입력폼에 입력 후 전송 시 SERVER DB에 입력 구현하기</p>
      <p>2. 로그인 - 로그인 폼을 활용하여 로그인 시 SERVER DB에 입력 구현하기</p>

    </main>

    <footer className="footer">
      <p>하단 푸터영역</p>
      <address>
        Copyright&copy;2025 OOOO allrights reserved.
      </address>
    </footer>
    </>
  );
}

export default App;
