import React from 'react';
import dummy from './data/orders.json'

function Orders(props) {
  return (
    <>
      <h3>Order json 데이터 출력 하기</h3>
      <ul>
        {dummy.map(order=>(
          <li>{order.o_day}, {order.g_code}, {order.o_num}, {order.c_name}</li>
        ))}
      </ul>
    </>
  );
}

export default Orders;