import React, {useState} from 'react';

function Loginform(props) {
  const [inputs, setInputs] = useState({username:'', userid:''});

  const onDataChange = (e) => {
    const {value, name} = e.target;
    setInputs({
      ...inputs, // 기존 input객체 복사
      [name]:value // name키를 가진 값을 value로 설정
    })
  }

  const onDataReset = (e) => {
    setInputs({username:'', userid:''});
  }

  return (
    <>
      <h3>useState함수를 활용하여 입력폼 '이름', '아이디' 입력하면 아래 내용 출력하고 '초기화'메뉴 클릭 시 내용 비우기</h3>
      <ul>
        <li>여러개의 input요소 사용 시 사용될 속성 name, value설정</li>
        <li>e.target.value // 이벤트를 받는 타겟의 value값</li>
        <li>e.target.name // 이벤트를 받는 타겟의 name값</li>
      </ul>

      <fieldset>
        <legend>폼 그룹</legend>
        <p><input type="text" placeholder='이름을 입력하세요.' value={inputs.username} name='username' onChange={onDataChange} /></p>
        <p><input type="text" placeholder='아이디를 입력하세요.' value={inputs.userid} name='userid' onChange={onDataChange} /></p>
        <input type="button" value='초기화' onClick={onDataReset} />
        <p>값 출력하기 : 이름 - {inputs.username}, ID - {inputs.userid}</p>
      </fieldset>

      <h3>보조설명</h3>
      <pre>
        {`
        const [inputs, setInputs] = useState(); // 값이 1개일 경우 useState()함수에 값을 그냥 적으면 기본값이 들어간다.
        하지만 상태값을 관리하기 위한 값이 2개 이기 때문에 2개를 관리하기 위해 따로 설정함.
        그럴때는 useState()함수에 객체로 입력하면 된다.
        `}
      </pre>
      <p>객체의 속성으로 값을 호출하는 방법은 아래와 같다. <br />
      inputs.username; <br /> inputs.userid;</p>
      <p>객체 비구조화 할당 방법으로 처리하기<br />
      const username, userid = inputs;</p>
      <p>할당 처리 후 호출하는 방법<br />
      username; <br />userid;<br />
      이렇게 작성하면 표현식으로 호출 시 변수명으로 적으면 되니 간단해짐.</p>
      <p>const &#123; value, name &#125; = e.target;<br />
      위 수식은 e.target에서 name, value를 추출하여 각각의 변수에 담는다 = 비구조화 할당방식</p>
      <p>
      <br />
      setinput(&#123;<br />
        ...inputs,<br />
        [name] : value<br />
      &#125;)
      <br />
      기존 inputs값을 복사해 오라는 의미로 나머지 패턴 ...을 사용한다. 이 때 객체의 모든 내용을 모두 펼쳐서 복사해준다.
      </p>
      <p>[name] : value에서 대괄호를 쓴것은 안쓰고 name:value로 쓰면 기존 상태는 제거가 된다. 객체 안에서 key[]로 감싸면 그 안에 넣은 속성명이 가리키는 실제값을 key값으로 사용된다.</p>
      <p>객체에 값을 할당할 때 동적인 키값으로 값을 할당할 때 대괄호를 사용한다.</p>
    </>
  );
}

export default Loginform;