import React, { useState } from 'react';

const TabMenu3 = (props) => {
  const [activeTab, setActiveTab] = useState('Home');
  const tabs=[
    {title:'Home', content:'메인'},
    {title:'About', content:'소개'},
    {title:'Contect', content:'문의하기'}
  ];

  return (
    <>
      <h3>Tabmnu3 - 배열객체를 생성하여 map함수로 콘텐츠 출력하기</h3>
      <ul className='menu_list'>
        {tabs.map((tab)=>(
          <li key={tab.title}
          onClick={()=>setActiveTab(tab.title)}
          className={tab.title===activeTab?'act':''}>
          {tab.title}
          </li>
        ))}
      </ul>
      <div className='content'>
          {activeTab==='Home'?tabs[0].content:null}
          {activeTab==='About'?tabs[1].content:null}
          {activeTab==='Contect'?tabs[2].content:null}
      </div>
    </>
  );
}

export default TabMenu3;