import React, {useState, useEffect} from 'react';
import dummy from './data/book_store.json';

function BookStore(props) {
  const [books, setBooks] = useState([]);  // 상태값 초기화
  const [loading, setLoading] = useState(true);  // 로딩 상태
  const [error, setError] = useState(null);  // 에러 상태

  useEffect(()=>{
    fetch(`${process.env.PUBLIC_URL}/data/book_store.json`)
    // fetch('/data/book_store.json')
      // .then(response => response.json())
      // .then(json => setBooks(json))
      .then((response) => {
        if (!response.ok) {
          throw new Error('파일을 불러오는 데 문제가 발생했습니다');
        }
        return response.json();
      })
      .then((json) => {
        setBooks(json);
        setLoading(false);
      })
      .catch((err) => {
        setError(err.message);
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>Error: {error}</p>;
  }

  return (
    <>
      <h3>BookStore json데이터 출력하기</h3>
      <p>1. 파일이 작은 경우 import방식으로 데이터 출력</p>
      <ul>
        {dummy.map(book=>(
          <li key={book.num}>
            {book.num}, {book.name}, {book.area1}, {book.area2}, {book.area3}, {book.BOOK_CNT}, {book.owner_nm}, {book.tel_num}
          </li>
        ))}
      </ul>

      <p>2. fetch를 활용한 비동기 방식으로 데이터 로딩하기</p>
      <ul>
        {books.map(book=>(
          <li key={book.num}>
            {book.num}, {book.name}, {book.area1}, {book.area2}, {book.area3}, {book.BOOK_CNT}, {book.owner_nm}, {book.tel_num}
          </li>
        ))}
      </ul>

    </>
  );
}

export default BookStore;