import React from 'react';
import musicData from './data/music_data.json';

function Music(props) {
  return (
    <>
      <p>map 함수 문법 - 기존 배열을 이용하여 새로운 배열을 만들고자 할 때 사용.</p>
      <pre>
        {`
          1. 배열 객체 생성
          const fruites = ['사과', '바나나', '오렌지'];

          2. 함수 안에 map함수로 배열데이터 출력
          <ul>
            {fruites.map((fruit, index) => (
              <li key={index}>{fruit}</li>
            ))}
          </ul>
        `}
      </pre>

      {/* <ul>
        {musicData.map((music, i)=>(
          <li key={music.albumId}>{music.id}, {music.title}, {music.url}, {music.thumbnailUrl}</li>
        ))}
      </ul> */}
      <dl>
        <dt>* axios</dt>
        <dd>- node.js와 브라우저를 위한 Promise 기반 HTTP 클라이언트입니다.</dd>
        <dd>- 서버사이드에서는 네이티브 node.js의 http 모듈을 사용하고, 클라이언트(브라우저)에서는 XMLHttpRequests를 사용합니다.</dd>
        <dd>- 브라우저, Node.js를 위한 Promise API를 활용하는 HTTP 비동기 통신 라이브러리이다. 쉽게 말해서 backend, frontend 통신을 쉽게 하기 위해 Ajax와 더불어 사용한다.</dd>
        <dd>- 자바스크립트에서는 fetch api가 있지만, 프레임워크에서 ajax를 구현할 때는 axios를 쓴다.</dd>
      </dl>
    </>
  );
}

export default Music;