import React, { useState } from 'react';

function Modal(props) {
  const [showModal, setShowModal] = useState(false); // false 버튼 클릭 시 나오게 하는 상태값

  return (
    <>
      <ul>
        <li>useState 훅 사용 : showModal이라는 상태 변수와 setShowModal이라는 상태 변경 함수를 선언했습니다. 초기값을 true로 설정하면 모달이 처음부터 표시되고, false로 설정하면 버튼 클릭시에만 표시됩니다.</li>
        <li>모달 트리거 : '모달 열기' 버튼을 클릭하면 setShowModal(true)가 실행되어 모달이 열립니다.</li>
        <li>모달 스타일링(style) : 외부 div(부모)는 배경색 검정, 투명도 적용하여 포지션 fixed로 고정, 왼쪽, 상단에 위치, 내부 div(자식)은 화면 중앙에 배치</li>
        <li>모달 닫기 : '닫기'버튼 클릭 시 setShowModal(false)를 실행하여 모달을 숨기도록 한다.</li>
      </ul>
      <button onClick={()=>setShowModal(true)}>모달 열기</button>
      {/* 조건부 렌더링 공식 {조건 && <태그> 내용 </태그>} */}
      {showModal &&(
        <div className='modal_wrap' style={{position:'fixed', width:'100%', height:'100%', top:'0px', left:'0px', background:'rgba(0,0,0,.5)', zIndex: 1000}}>
          <div className='modal' style={{width:'400px',position:'absolute',left:'50%',top:'50%',transform:'translate(-50%,-50%)'}}>
            <img src={`${process.env.PUBLIC_URL}/images/coupon.jpg`} alt="쿠폰이미지" style={{width:'100%'}} /><br />
            <button onClick={()=>setShowModal(false)} style={{float:'right'}}>닫기</button>
          </div>
        </div>
      )}
    </>
  );
}

export default Modal;