import React from 'react';
import './K_main.css';

function K_Header(props) {
  return (
    <>
      <h1 className="k_logo">
        <img src={`${process.env.PUBLIC_URL}/images/Kurly_logo.svg`} alt="컬리 로고"/>
      </h1>
      <hr className="k_hr" />
    </>
  );
}

export default K_Header;