import React, {useState} from 'react';
import './TabMenu.css';

const TabMenu2 = (props) => {
  const [menuNum, setMenuNum] = useState(1);
  
  return (
    <>
      <h3>탭 메뉴 구현하기</h3>
      <nav className='menu_list'>
        <ul>
          {/* 반복되는 부분은 배열 사용하여 반복 */}
          {[1,2,3,4].map((num) => 
            <li onClick={()=>setMenuNum(num)} className={menuNum === num?'act':''} key={num}>Menu{num}</li>
          )}
        </ul>
      </nav>
      <div className='tab_content'>
        {[1,2,3,4].map((num) =>
          menuNum === num ? <div key={num} className='content'>내용{num}</div> : null
        )}
      </div>
    </>
  );
}

export default TabMenu2;