import React from 'react';
import Header from './K_Header';
import Slide from './K_Slide';
import Random from './K_Random';
import Main from './K_Main';

function Kurly(props) {
  return (
    <>
      <Header />
      {/* 메인 슬라이드 컴포넌트 */}
      <Slide />
      {/* 랜덤 배너 컴포넌트 */}
      <Random />
      <Main />
    </>
  );
}

export default Kurly;