import React, { useEffect, useState } from 'react';

function Topbtn(props) {
  const scrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };
  const scrollDown = () => {
    window.scrollTo({
      top: document.body.scrollHeight,
      behavior: 'smooth'
    });
  };

  const [visibleUp, setVisibleUp] = useState(false);
  const [visibleDown, setVisibleDown] = useState(true);

  useEffect(() => {
    const handleScroll = () => {
      setVisibleUp(window.scrollY > 200); // 스크롤 위치가 200px 이상일 때 visible을 true로 설정
      setVisibleDown(window.scrollY < document.body.scrollHeight - window.innerHeight - 200); // 스크롤 위치가 페이지 하단에서 200px 이상일 때 visible을 true로 설정
    };
    window.addEventListener('scroll', handleScroll); // 스크롤 이벤트 리스너 추가
    return () => {
      window.removeEventListener('scroll', handleScroll); // 컴포넌트 언마운트 시 이벤트 리스너 제거
    };
  });

  return (
    <>
      {visibleUp &&(  // 상태 변수인 visible이 true일 때만 버튼을 보여줌
        <button onClick={scrollTop}
        style={{
          position: 'fixed',
          bottom: '100px',
          right: '50px',
          backgroundColor: '#007bff',
          color: 'white',
          border: 'none',
          borderRadius: '5px',
          padding: '10px 15px',
          cursor: 'pointer',
          zIndex: 999,
        }}>Top</button>)}

      {visibleDown &&(  // 상태 변수인 visible이 true일 때만 버튼을 보여줌
        <button onClick={scrollDown}
        style={{
          position: 'fixed',
          bottom: '50px',
          right: '50px',
          backgroundColor: '#007bff',
          color: 'white',
          border: 'none',
          borderRadius: '5px',
          padding: '10px 15px',
          cursor: 'pointer',
          zIndex: 999,
        }}>Down</button>)
      }
    </>
  );
}

export default Topbtn;