import React from 'react';
import item1 from '../images/item1.jpg';

function Class3(props) {
  return (
    <>
      <h3 className='title3'>02_리액트에서 이미지 삽입하는 방법</h3>
      <ul>
        <li>public 폴더에서 이미지 가져오기 위한 3가지 방법
          <ol>
            <li>src={`${process.env.PUBLIC_URL}/폴더명/파일명.확장자`}</li>
            <li>src={'/폴더명/파일명.확장자'}</li>
            <li>src={'폴더명.파일명.확장자'}</li>
          </ol>
        </li>
        <li>1. src폴더에 있는 이미지 : import
          <ol>
            <li>import logo from "./src/폴더명/파일명.확장자";</li>
            <li>img src={} alt="" 식으로 작성</li>
          </ol>
        </li>
        <li>2. src폴더에 있는 이미지 : require
          <p>img src=require('../images/logo.png').default alt=""</p>
        </li>
        <li>3. css에서 이미지 경로 설정</li>
      </ul>

      <img src={`${process.env.PUBLIC_URL}/images/Logo.png`} alt="로고" />
      <br />
      <img src={item1} alt="아이템1" />
      <br />
      <img src={require('../images/item2.jpg')} alt="" />
      <br />

      <p>jsx파일에서 절대경로는 public폴더를 기준으로 함.</p>
      <p>css파일에서 절대경로는 src폴더를 기준으로 함.</p>
      <p>리액트에서는 jsx파일로 작성 시 환경변수 PUBLIC_URL을 사용할 것을 권장.(github에 업로드 시에도 PUBLIC_URL로 설정된 이미지가 정상적으로 노출됨)</p>
    </>
  );
}

export default Class3;