import React from 'react';

function StudentInput({name, age, email, onCreate, onDataChange}) {
  const input_style={
    lineHeight:"25px",
    margin:"5px",
    padding:"3px 5px"
  }
  const border_style={
    border:"1px solid #ccc",
    width:"700px",
    textAlign:"center"
  }

  return (
    <div style={border_style}>
      <input style={input_style} type="text" name='name' value={name} onChange={onDataChange} placeholder='이름을 입력하세요' />
      <input style={input_style} type="text" name='age' value={age} onChange={onDataChange} placeholder='나이를 입력하세요' />
      <input style={input_style} type="email" name='email' value={email} onChange={onDataChange} placeholder='이메일을 입력하세요' />

      <button style={input_style} onClick={onCreate}>입력</button>
    </div>
  );
}

export default StudentInput;