import React, {useState} from 'react';

function Usestate(props) {
  const style={
    width:'300px',
    padding:'10px',
    margin:'10px',
    border:'3px solid #f00'
  }

  // num(상태변수명), setNum(상태 변경을 위한 함수명)
  const [num, setNum] = useState(1);
  const price = 7300;
  const total = (price*num).toLocaleString();

  const onIncrease=()=>{
    setNum(num+1);
  }
  const onDecrease=()=>{
    setNum(num-1);
    if(num<=1) setNum(1);
  }

  return (
    <>
      <h3 className='title3'>useState()함수를 통한 데이터 관리</h3>
      <p>클래스형 컴포넌트는 state값을 바로 사용할 수 있었습니다. 하지만 함수형 컴포넌트에서는 state값을 사용할 수 없었는데, 리액트 버전 16.8이상부터는 useState()함수가 추가되어 함수형 컴포넌트에서도 state값을 사용할 수 있게 되었습니다.</p>
      <p>state는 자신의 컴포넌트가 '유동적으로 변경될 값'을 관리할 때 사용합니다.</p>
      <p>useState()함수 사용 시 import React, &#123;useState&#125;함수를 추가해야 한다.</p>

      <pre>
        {`
        const [값변수, 값변경을 위한 함수] = useState(기본값);
        const 값변경을 위한 함수 = (); //내용
        const 상태값을 갖고 오는 변수 = useState(기본값);
        const 상태값 변수 = 상태 갖고 오는 변수[0];
        const 변경될 상태값 변수 = 상태 갖고 오는 변수[1];
        `}
      </pre>

      <div style={style}>
        <p>값 변화 : {num}</p>
        <input type="button" value="+" onClick={onIncrease} />
        <input type="button" value="-" onClick={onDecrease} />
        <p>상품가격 : 7,300원</p>
        <p>주문가격 : {total}원</p>
      </div>

    </>
  );
}

export default Usestate;