import React from 'react';

// function Child({name, age, hobby}) {
const Child=({name, age, hobby}) => {
  return (
    <>
      <h3>Child(자손) 컴포넌트</h3>
      <div>
        <ul>
          <li>이름 : {name}</li>
          <li>나이 : {age}</li>
          <li>취미 : {hobby}</li>
        </ul>
      </div>
      <p>children props는 부모컴포넌트에서 내려주지 않아도 컴포넌트 자체적으로 가지고 있는 props</p>
      <p>모든 클래스 컴포넌트는 this.props.children을 사용할 수 있음.</p>
    </>
  );
}

Child.defaultProps = {
  name:'-',
  age:'-',
  hobby:'-'
}

export default Child;