import React from 'react';
import dummy from './data/fruit.json';

function Fruits(props) {
  return (
    <>
      <h3>Fruits json 데이터 출력 하기</h3>
      <ul>
        {dummy.map(fruits=>(
          <li key={fruits.num}>{fruits.num}, {fruits.name}, {fruits.color}, {fruits.country}, {fruits.price}</li>
        ))}
      </ul>
    </>
  );
}

export default Fruits;