import React from 'react';
import dummy from './data/noodle.json'

function Noodle(props) {
  return (
    <>
      <h3>Noodle json 데이터 출력하기</h3>
      <ul>
        {dummy.map(noodle=>(
          <li key={noodle.num}>{noodle.num}, {noodle.name}, {noodle.company}, {noodle.kind}, {noodle.e_date}, {noodle.price}</li>
        ))}
      </ul>
    </>
  );
}

export default Noodle;