import React, { Component } from 'react';
import Counter1 from './Counter1';
import Counter2 from './Counter2';

class Class5 extends Component {
  render() {
    return (
      <>
        <h3>2. State정의와 사용</h3>
        <ul>
          <li>부모가 내려주는 값이 아닌, 컴포넌트 자신이 어떠한 값을 관리하고자 할 때 사용한다.</li>
          <li>클래스형 컴포넌트는 state를 바로 사용할 수 있다.</li>
          <li>함수형 컴포넌트는 useState() 함수를 사용해야 함 - 리액트 HOOKS라고 함</li>
          <li>리액트는 제이쿼리와는 달리 DOM을 직접 가져와서 조작하는 것이 아니기 때문에, 유동적으로 변화되는 값은 모두 state로 관리해야 한다고 보면 됨.</li>
        </ul>

        <p>1. 자신 컴포넌트에서 유동적으로 변할 값은 state로 선언합니다.</p>
        <p>2. setState()함수는 state값을 변경해주는 함수이다.</p>
        <p>3. jsx문법에서는 반드시 대, 소문자를 확인해야 한다.</p>

        <dl>
          <dt>React Hooks소개</dt>
          <dd>Hooks는 리액트 버전 16.9부터 추가되었습니다.</dd>
          <dd>Hooks가 나온 것은 함수 컴포넌트가 리액트의 몇 가지 기능에 접근할 수 없었던 것을 접근하게 해주기 위해서 입니다.</dd>
          <dd>Hooks를 사용하는 이유는 state 및 라이프사이클에 함수 컴포넌트가 접근할 수 있게 합니다.</dd>
          <dd>Hooks는 React의 함수컴포넌트 내에서만 호출할 수 있습니다.</dd>
          <dd>Hooks는 컴포넌트의 최상위 수준에서만 호출할 수 있습니다.</dd>
          <dd>Hooks는 조건부일 수는 없습니다.</dd>
        </dl>

        <h4>1. Class형 컴포넌트로 카운터 만들기</h4>
        <Counter1 />

        <h4>2. function형 컴포넌트로 카운터 만들기</h4>
        <Counter2 />
      </>
    );
  }
}

export default Class5;